<template>
  <div class="container-fluid px-4 px-md-5 py-5 px-lg-1 px-xl-5 mx-auto">
    <div class="card card0 border-0">
      <div class="row d-flex">
        <div class="col-lg-6 d-flex justify-content-center pt-5">
          <img
            src="../assets/sarsPassLogoVertical.png"
            alt="Responsive image"
            style="max-height: 150px"
          />
        </div>
        <div class="col-lg-6">
          <div class="card2 card border-0 px-4 py-5">
            <div class="row px-3 pb-3">
              <label class="mb-1">
                <h6 class="mb-0 text-sm">Dni</h6>
              </label>
              <input
                v-model="dni"
                type="text"
                class="form-control bg-light"
                id="exampleInputDNI1"
                aria-describedby="DNI"
                placeholder="DNI"
              />
            </div>
            <div class="row px-3 pb-5">
              <label class="mb-1">
                <h6 class="mb-0 text-sm">Contrase単a</h6>
              </label>
              <input
                v-model="password"
                type="password"
                class="form-control bg-light"
                id="exampleInputPassword1"
                placeholder="Contrase単a"
              />
            </div>
            <div class="row px-3 mb-4"></div>
            <div class="row mb-3 px-3">
              <button
                @click="logIn(dni, password)"
                type="button"
                class="btn btn-primary"
              >
                Login
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import firebase from "firebase";

export default {
  name: "Login",

  data() {
    return {
      email: "",
      password: "",
      name: "",
      dni: "",
      arrayUsers: [],
    };
  },
  props: {},
  methods: {
    logUser(users) {
      this.arrayUsers = [];
      for (let key in users) {
        this.arrayUsers.push({
          name: users[key].name,
          password: users[key].password,
          dni: users[key].dni,
          id: users[key].id,
          vaccine: users[key].vaccine,
          centerVaccine: users[key].centerVaccine,
          dateVaccine: users[key].dateVaccine,
          rol: users[key].rol,
          sexo: users[key].sexo,
          isVacunado: users[key].isVacunado,
        });
      }
    },
    logIn(dni, password) {
      window.localStorage.removeItem("userLogged");
      for (let user in this.arrayUsers) {
        if (
          this.arrayUsers[user].dni === dni &&
          this.arrayUsers[user].password === password
        ) {
          let tempKey = this.getRandomKey();
          firebase
            .database()
            .ref("users/" + this.arrayUsers[user].dni)
            .set({
              name: this.arrayUsers[user].name,
              password: this.arrayUsers[user].password,
              dni: this.arrayUsers[user].dni,
              id: tempKey,
              vaccine: this.arrayUsers[user].vaccine,
              centerVaccine: this.arrayUsers[user].centerVaccine,
              dateVaccine: this.arrayUsers[user].dateVaccine,
              rol: this.arrayUsers[user].rol,
              sexo: this.arrayUsers[user].sexo,
              isVacunado: this.arrayUsers[user].isVacunado,
            });
          let userObj = {
            name: this.arrayUsers[user].name,
            password: this.arrayUsers[user].password,
            dni: this.arrayUsers[user].dni,
            id: tempKey,
            vaccine: this.arrayUsers[user].vaccine,
            centerVaccine: this.arrayUsers[user].centerVaccine,
            dateVaccine: this.arrayUsers[user].dateVaccine,
            rol: this.arrayUsers[user].rol,
            sexo: this.arrayUsers[user].sexo,
            isVacunado: this.arrayUsers[user].isVacunado,
          };
          this.userControl = {
            dni: this.arrayUsers[user].dni,
            pass: this.arrayUsers[user].password,
            rol: this.arrayUsers[user].rol,
          };
          localStorage.setItem("userControl", JSON.stringify(this.userControl));
          localStorage.setItem("userLogged", JSON.stringify(userObj));
          this.$router.push({
            name: "user",
            params: {
              id: tempKey,
            },
          });
        }
      }
    },
    resetVars() {
      this.email = "";
      this.password = "";
      this.name = "";
      this.dni = "";
    },
    getRandomKey() {
      const randomChars =
        "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
      let result = "";
      for (let i = 0; i < 25; i++) {
        result += randomChars.charAt(
          Math.floor(Math.random() * randomChars.length)
        );
      }
      return result;
    },
  },

  mounted() {
    firebase
      .database()
      .ref("users/")
      .on("value", (snapshots) => this.logUser(snapshots.val()));
  },
};
</script>

<style>
