<template>
  <div class="container py-5 px-5">
    <div class="row">
      <div class="col-12">
        
        <form class="card " >
          <p class="card-header"> Registrar nuevo usuario</p>
          <div class="card-body">
          <div class="form-group">
            <label for="exampleInputName1">Nombre completo</label>
            <input v-model="name" type="name" class="form-control" id="exampleInputName1" aria-describedby="Name"
                   placeholder="Nombre completo">
          </div>
          <div class="form-group">
            <label for="exampleInputDNI">DNI</label>
            <input v-model="dni" type="text" class="form-control" id="exampleInputDNI" aria-describedby="DNI"
                   placeholder="DNI">
          </div>
          <div class="form-group">
            <label for="exampleInputPassword">Contrase単a</label>
            <input v-model="password" type="password" class="form-control" id="exampleInputPassword"
                   placeholder="Contrase単a">
          </div>
          <div class="form-group">
            <label for="exampleInputPassword">Sexo</label>
            <select v-model="sexo" class="form-select" aria-label="Sexo">
              <option selected>Sexo</option>
              <option value="Hombre">Hombre</option>
              <option value="Mujer">Mujer</option>
            </select>
          </div>
          <div class="form-group">
            <label for="exampleInputPassword">Vacunado</label>
            <select v-model="isVacunado" class="form-select" aria-label="Vacunado">
              <option selected>Vacunado</option>
              <option value="Si">Si</option>
              <option value="No">No</option>
            </select>
          </div>
          <div class="form-group">
            <label for="exampleInputPassword">Rol de usuario</label>
            <select v-model="rol" class="form-select" aria-label="Rol of user">
              <option selected>rol</option>
              <option value="Admin">Admin</option>
              <option value="User">User</option>
              <option value="Sanitary">Sanitary</option>
              <option value="Control">Control</option>
            </select>
          </div>
          <div class="form-group">
            <label for="exampleInputVaccine">Vaccine Brand</label>
            <input v-model="vaccine" type="text" class="form-control" id="exampleInputVaccine"
                   placeholder="Vaccine">
          </div>
          <div class="form-group">
            <label for="exampleInputCenterVaccine">Center of Vaccine</label>
            <input v-model="centerVaccine" type="text" class="form-control" id="exampleInputCenterVaccine"
                   placeholder="Center Vaccine">
          </div>
          <div class="form-group">
            <label for="exampleInputDateVaccine">Date of Vaccine</label>
            <input v-model="dateVaccine" type="text" class="form-control" id="exampleInputDateVaccine"
                   placeholder="Date Vaccine">
          </div>
          <br>
          <button @click="registerNewUser()" type="button" class="btn btn-primary">register</button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import firebase from "firebase";

export default {
  name: 'registerNewUser',
  data() {
    return {
      password: '',
      name: '',
      dni: '',
      arrayUsers: [],
      vaccine: '',
      centerVaccine: '',
      dateVaccine: '',
      rol: '',
      sexo: '',
      isVacunado: '',

    }
  },
  props: {},
  methods: {
    resetVars() {
      this.password = '';
      this.name = '';
      this.dni = '';
      this.vaccine = '';
      this.centerVaccine = '';
      this.dateVaccine = '';
      this.rol = '';
      this.sexo = '';
      this.isVacunado = '';

    },
    getRandomKey() {
      const randomChars = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
      let result = '';
      for (let i = 0; i < 25; i++) {
        result += randomChars.charAt(Math.floor(Math.random() * randomChars.length));
      }
      return result;
    },
    registerNewUser() {
      let tempKey = this.getRandomKey();
      firebase.database().ref('users/' + this.dni).set({
        name: this.name,
        password: this.password,
        dni: this.dni,
        id: tempKey,
        vaccine: this.vaccine,
        centerVaccine: this.centerVaccine,
        dateVaccine: this.dateVaccine,
        rol: this.rol,
        sexo: this.sexo,
        isVacunado: this.isVacunado
      }).then(() => {
        console.log('New user registered.');
      });
      this.resetVars();
    }
  },
}
</script>

