<template>
  <div class="container px-5 py-5 d-flex justify-content-center">
    <div class="card" @click="goToInfo" style="width: 18rem">
      <h3 class="card-title text-center mt-5">QR de usuario</h3>
      <vue-qrcode :key="componentKey" :value="url" />
      <div class="card-body">
        <h3 class="card-title text-center">{{ this.infoUser.name }}</h3>
        <p class="card-text display-4 text-center" id="demo"></p>
        <div class="progress">
          <div
            class="progress-bar progress-bar-striped bg-info"
            id="bar"
            role="progressbar"
            aria-valuenow="0"
            aria-valuemin="0"
            aria-valuemax="30000"
            style="width: 0%"
          ></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import VueQrcode from "vue-qrcode";
import firebase from "firebase";

export default {
  name: "UserComp",
  data() {
    return {
      infoUser: "",
      url: "",
      componentKey: 0,
      localLog: "",
      longBar: 0,
      barNow: 1,
      timer: "",
      forceRender: "",
    };
  },
  components: {
    VueQrcode,
  },
  props: {},
  methods: {
    setTimer() {
      var countDownDate = new Date().getTime();

      countDownDate += 1000 * 30;

      this.timer = setInterval(function () {
        // Get today's date and time
        var now = new Date().getTime();

        // Find the distance between now and the count down date
        var distance = countDownDate - now;
        this.longBar = distance;
        // Time calculations for days, hours, minutes and seconds

        var seconds = Math.floor((distance % (1000 * 60)) / 1000) + 1;

        // Display the result in the element with id="demo"
        document.getElementById("demo").innerHTML = +seconds + "s ";
        document.getElementById("bar").removeAttribute("aria-valuenow");

        if (
          100 - (seconds / 30) * 100 >= 66 &&
          100 - (seconds / 30) * 100 <= 85
        ) {
          document
            .getElementById("bar")
            .setAttribute(
              "class",
              "progress-bar progress-bar-striped bg-warning"
            );
        } else if (100 - (seconds / 30) * 100 < 66) {
          document
            .getElementById("bar")
            .setAttribute("class", "progress-bar progress-bar-striped bg-info");
        } else if (100 - (seconds / 30) * 100 > 85) {
          document
            .getElementById("bar")
            .setAttribute(
              "class",
              "progress-bar progress-bar-striped bg-danger"
            );
        }
        document
          .getElementById("bar")
          .setAttribute(
            "Style",
            "width: " + (100 - (seconds / 30) * 100) + "%"
          );

        // If the count down is finished, write some text

        if (distance <= 0) {
          countDownDate += 1000 * 30;
        }
      }, 10);
    },
    goToInfo() {
      this.$router.push({
        name: "check",
        params: {
          id: this.infoUser.id,
        },
      });
    },
    reload(ide) {
      let temp = localStorage.getItem("userLogged");
      this.localLog = JSON.parse(temp);
      firebase
        .database()
        .ref("users/" + this.localLog.dni)
        .set({
          name: this.localLog.name,
          password: this.localLog.password,
          dni: this.localLog.dni,
          id: ide,
          vaccine: this.localLog.vaccine,
          centerVaccine: this.localLog.centerVaccine,
          dateVaccine: this.localLog.dateVaccine,
          rol: this.localLog.rol,
          sexo: this.localLog.sexo,
          isVacunado: this.localLog.isVacunado,
        });
      let userObj = {
        name: this.localLog.name,
        password: this.localLog.password,
        dni: this.localLog.dni,
        id: ide,
        vaccine: this.localLog.vaccine,
        centerVaccine: this.localLog.centerVaccine,
        dateVaccine: this.localLog.dateVaccine,
        rol: this.localLog.rol,
        sexo: this.localLog.sexo,
        isVacunado: this.localLog.isVacunado,
      };
      localStorage.setItem("userLogged", JSON.stringify(userObj));
      this.infoUser = JSON.parse(localStorage.getItem("userLogged"));
    },
    forceRerender() {
      this.componentKey += 1;
      let tempKey = this.getRandomKey();
      this.url = 'https://sars-pass.netlify.app/#/check/' + tempKey;
      //this.url = "http://192.168.0.40:8080/#/check/" + tempKey;
      this.reload(tempKey);
    },
    getRandomKey() {
      const randomChars =
        "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
      let result = "";
      for (let i = 0; i < 25; i++) {
        result += randomChars.charAt(
          Math.floor(Math.random() * randomChars.length)
        );
      }
      return result;
    },
  },
  beforeDestroy() {
  
    clearInterval(this.timer);
    clearInterval(this.forceRender);
   
  },

  mounted() {
    this.infoUser = JSON.parse(localStorage.getItem("userLogged"));
    this.url = 'https://sars-pass.netlify.app/#/check/' + this.infoUser.id;
    //this.url = "http://192.168.0.40:8080/#/check/" + this.infoUser.id;
    clearInterval(this.interval);
    clearInterval(this.forceRender);
    this.forceRender = setInterval(this.forceRerender, 30000);
    this.setTimer();
  },
};
</script>

