<template>
  <div class="">
    <div v-show="!this.show">
      <div class="card card0 border-0 px-5 py-5 mx-5 my-5">
        <div class="row d-flex">
          <div class="col-lg-6 d-flex justify-content-center pt-5">
            <img
              src="../assets/sarsPassLogoVertical.png"
              alt="Responsive image"
              style="max-height: 200px"
            />
          </div>
          <div class="col-lg-6">
            <div class="card2 card border-0 px-4 py-5">
              <div class="row px-3 pb-3">
                <label class="mb-1">
                  <h6 class="mb-0 text-sm">Dni</h6>
                </label>
                <input
                  v-model="dniForm"
                  type="text"
                  class="form-control"
                  id="exampleInputDNI1"
                  aria-describedby="DNI"
                  placeholder="DNI"
                />
              </div>
              <div class="row px-3 pb-5">
                <label class="mb-1">
                  <h6 class="mb-0 text-sm">Contrase単a</h6>
                </label>
                <input
                  v-model="passForm"
                  type="password"
                  class="form-control"
                  id="exampleInputPassword1"
                  placeholder="Contrase単a"
                />
              </div>
              <div class="row px-3 mb-4"></div>
              <div class="row mb-3 px-3">
                <button
                  @click="logUser(dniForm, passForm)"
                  type="button"
                  class="btn btn-primary"
                >
                  Login
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div v-show="this.show">
      <div class="row justify-content-center">
        <div class="col-5 py-5 my-5">
          <div v-show="rol === 'Admin'">
            <div id="info">
              <div v-if="userCheck !== null">
                <div class="card">
                  <h5 class="card-header">{{ this.userCheck.name }}</h5>
                  <div class="card-body">
                    <h5 class="card-title">Informacion del usuario</h5>
                    <p class="card-text">DNI: {{ this.userCheck.dni }}</p>
                    <p class="card-text">
                      Esta vacunado: {{ this.userCheck.isVacunado }}
                    </p>
                    <p class="card-text">
                      Vacuna: {{ this.userCheck.vaccine }}
                    </p>
                    <p class="card-text">
                      Centro de vacunacion: {{ this.userCheck.centerVaccine }}
                    </p>
                    <p class="card-text">
                      Fecha de vacunacion{{ this.userCheck.dateVaccine }}
                    </p>
                    <p class="card-text">Sexo: {{ this.userCheck.sexo }}</p>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div v-show="rol === 'Control'">
            <div id="info">
              <div v-if="userCheck !== null">
                <div class="card">
                  <h5 class="card-header">{{ this.userCheck.name }}</h5>
                  <div class="card-body">
                    <h5 class="card-title">Informacion del usuario</h5>
                    <p class="card-text">DNI: {{ this.userCheck.dni }}</p>
                    <p class="card-text">
                      Esta vacunado: {{ this.userCheck.isVacunado }}
                    </p>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div v-show="rol === 'Sanitary'">
            <div id="info">
              <div v-if="userCheck !== null">
                <div v-if="modi" class="card">
                  <h5 class="card-header">{{ this.userCheck.name }}</h5>
                  <div class="card-body">
                    <h5 class="card-title">Informacion del usuario</h5>
                    <p class="card-text">DNI: {{ this.userCheck.dni }}</p>
                    <p class="card-text">
                      Esta vacunado: {{ this.userCheck.isVacunado }}
                    </p>
                    <p class="card-text">
                      Vacuna: {{ this.userCheck.vaccine }}
                    </p>
                    <p class="card-text">
                      Centro de vacunacion: {{ this.userCheck.centerVaccine }}
                    </p>
                    <p class="card-text">
                      Fecha de vacunacion{{ this.userCheck.dateVaccine }}
                    </p>
                    <p class="card-text">Sexo: {{ this.userCheck.sexo }}</p>
                    <button type="button" @click="mod" class="btn btn-primary">
                      Modificar
                    </button>
                  </div>
                </div>
                <div class="card" v-if="!modi"> 
                <h1 class="card-header">Actualizar usuario </h1>
                <form class="card-body">
                  <h5>{{ this.userCheck.name }}</h5>
                  <div class="form-group">
                    <label for="exampleInputPassword">Vacunado</label>
                    <select
                      v-model="isVacunado"
                      class="form-select"
                      aria-label="Vacunado"
                    >
                      <option selected>Vacunado</option>
                      <option value="Si">Si</option>
                      <option value="No">No</option>
                    </select>
                  </div>
                  <div class="form-group">
                    <label for="exampleInputVaccine">Vaccine Brand</label>
                    <input
                      v-model="vaccine"
                      type="text"
                      class="form-control"
                      id="exampleInputVaccine"
                      placeholder="Vaccine"
                    />
                  </div>
                  <div class="form-group">
                    <label for="exampleInputCenterVaccine"
                      >Center of Vaccine</label
                    >
                    <input
                      v-model="centerVaccine"
                      type="text"
                      class="form-control"
                      id="exampleInputCenterVaccine"
                      placeholder="Center Vaccine"
                    />
                  </div>
                  <div class="form-group">
                    <label for="exampleInputDateVaccine">Date of Vaccine</label>
                    <input
                      v-model="dateVaccine"
                      type="text"
                      class="form-control"
                      id="exampleInputDateVaccine"
                      placeholder="Date Vaccine"
                    />
                  </div>
                  <br />
                  <div class="row d-flex justify-content-around">
                  <div class="col-3 d-flex justify-content-around"><button
                    @click="
                      updateUser(
                        isVacunado,
                        vaccine,
                        dateVaccine,
                        centerVaccine
                      )
                    "
                    type="button"
                    class="btn btn-primary"
                  >
                    Modificar
                  </button></div>
                  <div class="col-3 d-flex justify-content-around"><button type="button" @click="mod" class="btn btn-danger" style="align-self: right; !important">
                      Cancelar
                    </button></div>
                  
                  
                    </div>
                </form>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import firebase from "firebase";

export default {
  name: "checkUser",
  data() {
    return {
      key: "",
      dniForm: "",
      passForm: "",
      arrayUsers: [],
      show: false,
      userCheck: null,
      rol: "",
      userControl: {},
      localLog: [],
      vaccine: "",
      centerVaccine: "",
      isVacunado: "",
      dateVaccine: "",
      modi: false
    };
  },
  props: {},
  methods: {
    loadUsers: function (users) {
      this.arrayUsers = [];
      for (let key in users) {
        this.arrayUsers.push({
          name: users[key].name,
          password: users[key].password,
          rol: users[key].rol,
          dni: users[key].dni,
          id: users[key].id,
          vaccine: users[key].vaccine,
          centerVaccine: users[key].centerVaccine,
          dateVaccine: users[key].dateVaccine,
          sexo: users[key].sexo,
          isVacunado: users[key].isVacunado,
        });
      }
      this.autoLogger();
    },
    mod(){
      this.modi = !this.modi;
    },
    logUser(user, pass) {
      //Esta logeado
      if (this.localLog === null) {
        this.dniForm = user;
        this.passForm = pass;
        for (user in this.arrayUsers) {
          if (
            this.dniForm === this.arrayUsers[user].dni &&
            this.passForm === this.arrayUsers[user].password
          ) {
            if (
              this.arrayUsers[user].rol === "Admin" ||
              this.arrayUsers[user].rol === "Sanitary" ||
              this.arrayUsers[user].rol === "Control"
            ) {
              this.check(this.arrayUsers[user]);
              this.userControl = {
                dni: this.dniForm,
                pass: this.passForm,
                rol: this.arrayUsers[user].rol,
              };
              localStorage.setItem(
                "userControl",
                JSON.stringify(this.userControl)
              );
              this.autoLogger();
              this.show = true;
            }
          }
        }
      }
    },
    //Esta previamente logeado
    autoLogger() {
      this.key = this.$route.params.id;
      let temp = localStorage.getItem("userControl");
      this.localLog = JSON.parse(temp);
      if (this.localLog !== null) {
        for (let user in this.arrayUsers) {
          if (
            this.localLog.dni === this.arrayUsers[user].dni &&
            this.localLog.pass === this.arrayUsers[user].password
          ) {
            if (
              this.localLog.rol === "Admin" ||
              this.localLog.rol === "Sanitary" ||
              this.localLog.rol === "Control"
            ) {
              this.check(this.arrayUsers[user].rol);
            }
          }
        }
      } else {
        this.show = false;
      }
    },
    check(rol) {
      for (let user in this.arrayUsers) {
        if (this.key === this.arrayUsers[user].id) {
          this.userCheck = {
            name: this.arrayUsers[user].name,
            dni: this.arrayUsers[user].dni,
            id: this.arrayUsers[user].id,
            vaccine: this.arrayUsers[user].vaccine,
            centerVaccine: this.arrayUsers[user].centerVaccine,
            dateVaccine: this.arrayUsers[user].dateVaccine,
            sexo: this.arrayUsers[user].sexo,
            isVacunado: this.arrayUsers[user].isVacunado,
          };
          this.vaccine = this.arrayUsers[user].vaccine;
          this.centerVaccine = this.arrayUsers[user].centerVaccine;
          this.isVacunado = this.arrayUsers[user].isVacunado;
          this.dateVaccine = this.arrayUsers[user].dateVaccine;
        }
      }
      this.rol = rol;
      this.show = true;
    },
    updateUser() {
      for (let user in this.arrayUsers) {
        if (this.key === this.arrayUsers[user].id) {
          firebase
            .database()
            .ref("users/" + this.arrayUsers[user].dni)
            .set({
              name: this.arrayUsers[user].name,
              password: this.arrayUsers[user].password,
              dni: this.arrayUsers[user].dni,
              id: this.key,
              vaccine: this.vaccine,
              centerVaccine: this.centerVaccine,
              dateVaccine: this.dateVaccine,
              rol: this.arrayUsers[user].rol,
              sexo: this.arrayUsers[user].sexo,
              isVacunado: this.isVacunado,
            })
            .then(() => {
              console.log("User updated sucessfully!");
            });
        }
      }
      this.mod();
    },
  },
  mounted() {
    this.show = false;
    this.key = "";
    firebase
      .database()
      .ref("users/")
      .on("value", (snapshots) => this.loadUsers(snapshots.val()));
  },
  beforeDestroy() {},
};
</script>

<style scoped>
</style>